@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: white;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.workload__filter {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding: 50px 0 0;
}

.workload__filter-functional {
  width: 400px;
}

.workload__filter-functional-by-time {
  width: 100%;
  margin: 0 0 10px;
}

.workload__filter-functional-by-time-list {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 0;
  margin: 0;
}

.workload__filter-functional-by-time-button {
  width: 100%;
  background: transparent;
  border: none;
  border-radius: 5px;
  padding: 10px 0;
}

.workload__filter-functional-by-time-button--active {
  color: white;
  background: blue;
}

.workload__filter-functional-kinds-of-chart-list {
  list-style: none;
  line-height: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0 10px;
  padding: 0;
  margin: 0;
}

.workload__filter-functional-kinds-of-chart-button {
  width: 100%;
  height: 100%;
  background: transparent;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 10px 0;
}

.workload__filter-functional-kinds-of-chart--active {
  color: white;
  background: blue;
}

.workload__filter-statistics {
  width: 300px;
}

.workload__filter-statistics-wrapper {
  width: 100%;
  height: 100%;
}

.workload__filter-statistics-table {
  width: 100%;
  height: 100%;
  position: relative;
  border: 1px solid grey;
  border-radius: 5px;
}
.workload__filter-statistics-table::before {
  content: "";
  width: calc(100% - 20px);
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: grey;
}

.workload__filter-statistics-table-row {
  height: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 20px;
  padding: 0 20px;
}

.workload__filter-statistics-table-header-cell {
  width: 75%;
  text-align: left;
  padding: 0;
}

.workload__filter-statistics-table-description-cell {
  width: 25%;
  text-align: center;
  padding: 0;
}

g.tooltips {
  width: 175px !important;
  height: 175px !important;
  border-radius: 50%;
}

rect#chart-tooltip {
  width: 175px !important;
  height: 175px !important;
  border-radius: 50% !important;
}

text#chart-tooltip-label-text {
  display: block;
  text-align: center;
}

.ct-line.ct-threshold-above, .ct-point.ct-threshold-above, .ct-bar.ct-threshold-above {
  stroke: #f05b4f !important;
}

.ct-line.ct-threshold-middle, .ct-point.ct-threshold-middle, .ct-bar.ct-threshold-middle {
  stroke: black !important;
}

.ct-line.ct-threshold-below, .ct-point.ct-threshold-below, .ct-bar.ct-threshold-below {
  stroke: #59922b !important;
}

.ct-area.ct-threshold-above {
  fill: #f05b4f !important;
}

.ct-area.ct-threshold-middle {
  fill: black !important;
}

.ct-area.ct-threshold-below {
  fill: #59922b !important;
}